import Vue from 'vue'
import Router from 'vue-router'
import Layout from '@/views/layout/Layout'
Vue.use(Router)

export const constantRouterMap = [
  {
    path: '',
    component: Layout,
    redirect: 'dashboard',
    children: [
      {
        path: 'dashboard',
        component: () => import('@/views/dashboard/index'),
        name: 'dashboard',
        meta: { title: '', icon: 'dashboard', noCache: true, roles: [''] }
      }
    ]
  },
  {
    path: '/login',
    meta: {roles: ['']},
    component: () => import('@/views/login/index'),
    hidden: true
  },
  {
    path: '/auth-redirect',
    meta: {roles: ['']},
    component: () => import('@/views/login/authredirect'),
    hidden: true
  },
  {
    path: '/404',
    meta: {roles: ['']},
    component: () => import('@/views/errorPage/404'),
    hidden: true
  },
  {
    path: '/401',
    meta: {roles: ['']},
    component: () => import('@/views/errorPage/401'),
    hidden: true
  }]
export const asyncRouterMap = [
  {
    path: '/sys',
    component: Layout,
    name: 'sysManage',
    meta: {
      roles: [''],
      title: '',
      icon: 'chart'
    },
    children: [
      {
        path: 'admin',
        component: () => import('@/views/sys/admin'),
        name: 'admin',
        meta: {roles: [''], title: '', noCache: true}
      },
      {
        path: 'role',
        component: () => import('@/views/sys/role'),
        name: 'role',
        meta: {roles: [''], title: '', noCache: true}
      }
    ]
  },
  {
    path: '/info',
    component: Layout,
    redirect: 'noredirect',
    name: 'userManager',
    meta: {
      roles: [''],
      title: '',
      icon: 'chart'
    },
    children: [
      {
        path: 'user',
        component: import('@/views/info/user'),
        name: 'user',
        meta: {title: '', roles: ['']}
      },
      {
        path: 'plot',
        component: import('@/views/info/plot'),
        name: 'plot',
        meta: {title: '', roles: ['']}
      },
      {
        path: 'address',
        component: import('@/views/info/address'),
        name: 'address',
        meta: {title: '', roles: ['']}
      },
      {
        path: 'device',
        component: import('@/views/info/device'),
        name: 'device',
        meta: {title: '', roles: ['']}
      },
      {
        path: 'room',
        component: import('@/views/info/room'),
        name: 'room',
        meta: {title: '', roles: ['']}
      }
    ]
  },
  {
    path: '/fault',
    component: Layout,
    redirect: 'noredirect',
    alwaysShow: true,
    name: 'fault',
    meta: {
      title: '',
      icon: 'chart',
      roles: ['']
    },
    children: [
      {
        path: 'faultRecords',
        component: () => import('@/views/fault/faultRecords'),
        name: 'faultRecords',
        meta: {
          roles: [''],
          title: '',
          noCache: true
        }
      }, {
        path: 'realTimeWater',
        component: () => import('@/views/fault/realTimeWater'),
        name: 'realTimeWater',
        meta: {
          roles: [''],
          title: '',
          noCache: true
        }
      }, {
        path: 'plotFactor',
        component: () => import('@/views/fault/plotFactor'),
        name: 'plotFactor',
        meta: {
          roles: [''],
          title: '',
          noCache: true
        }
      }, {
        path: 'factorUpdateRecord',
        component: () => import('@/views/fault/factorUpdateRecord'),
        name: 'factorUpdateRecord',
        meta: {
          roles: [''],
          title: '',
          noCache: true
        }
      }]
  },
  {
    path: '/faultLeader',
    component: Layout,
    redirect: 'noredirect',
    alwaysShow: true,
    name: 'faultLeader',
    meta: {
      roles: [''],
      title: '',
      icon: 'chart'
    },
    children: [{
      path: 'faultRecords',
      component: () => import('@/views/fault/faultRecords'),
      name: 'faultRecordsLeader',
      meta: {
        roles: [''],
        title: '',
        noCache: true
      }
    }, {
      path: 'deviceMonitor',
      component: () => import('@/views/fault/deviceMonitor'),
      name: 'deviceMonitor',
      meta: {
        roles: [''],
        title: '',
        noCache: true
      }
    }]
  }
]
export default new Router({
  scrollBehavior: () => ({y: 0}),
  routes: constantRouterMap
})



